import React, { Component } from 'react';
import {  toast } from 'react-toastify';
import { Redirect,Link } from 'react-router-dom';
class CheckOutPage extends Component {
   
     constructor(props)
     {
         super(props);
         let loggedIn=true;
         let token=localStorage.getItem("token");
         if(token==null)
         {
             loggedIn=false;
             
         }
        
         this.state={loggedIn};
     }
     componentDidMount(){
    toast.success('Welcome Back !');
     }
     handleLogout=()=>{
         localStorage.removeItem("token");
       const loggedIn=false;
        this.setState({loggedIn});
     }
    render() { 
      
        let i=1;
        if(this.state.loggedIn===false)
        {
            return <Redirect to="/login" />
        }
        const  styles={marginTop:150};
        return ( <div style={styles} className="container">
           
           <div className="card">
               <div className="card-header d-flex justify-content-center">
                   <h4>Summary</h4>
               </div>
               <div className="card-body">
                   <table className="table">
                       <thead className="thead-light">
                       <tr className="row">
                           <th className="col-2">SNo.</th>
                           <th className="col-2">Item</th>
                           <th className="col-3">Price</th>
                           <th className="col-3">Number of Kg(s)</th>
                           <th className="col-2">Price</th>
                       </tr>
                       </thead>
                       <tbody>
                        { this.props.foods.map((food)=>{
                            if(food.count>0)
                            {
                                return <tr className="row">
                                    <td className="col-2">{i++}</td>
                                    <td className="col-2">{food.name}</td>
                                    <td className="col-3">$ {food.price}</td>
                                    <td className="col-3">{food.count}</td>
                                    <td className="col-2"> $ {food.count * food.price}</td>
                                </tr>
                            }
                        })}
                        <tr className="row mt-2">
                        <td className="col-10"><h3>Grand Total :</h3></td>
                        <td className="col "> $ {this.props.tc}</td>
                        </tr>
                       </tbody>
                   </table>
               </div>
               <div className="card-footer">
                   <div className="row">
                       <div className="col-10">
                          <Link to="/thanks"><button className="btn-warning btn-lg btn">Place Order</button></Link> 
                       </div>
                       <div className="col">
                       <button className="btn btn-danger btn-lg" onClick={this.handleLogout}> Logout </button>
                       </div>
                   </div>

              
               </div>
           </div>
          
           

           
        </div> );
    }
}
 
export default CheckOutPage;