import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';

import '../../node_modules/bootstrap/dist/css/bootstrap.css';

class Login extends Component {
    state = { 
        loggedIn:false,
        data :{ username:"",
        password:""}
       
     }
     constructor(props)
     {
         super(props);
         let loggedIn=true;
         const token=localStorage.getItem("token");
         if(token==null)
         {
             loggedIn=false;
            
         }
        this.setState({loggedIn});
     }
    handleChange=(e)=>{
        let data={...this.state.data};
        data[e.currentTarget.name]=e.currentTarget.value;
        this.setState({data});
     }
     handleSubmit=(e)=>{
         e.preventDefault();
         const {username,password}=this.state.data;
         if(username=="admin" && password=="12345")
         {
             localStorage.setItem("token","abcde");
             const loggedIn=true;
             this.setState({loggedIn});
         }
     }
    render() { 
       
       const  styles={marginTop:150};
        if(this.state.loggedIn===true)
        {
          
            return <Redirect to="/checkout" />
        }
        return ( <div className="container" style={styles}>
            
           <div className="form card">
                <div className="card-header d-flex justify-content-center">
                    <h3>Log-In</h3>
                </div>
                <div className="card-body">
                    <form onSubmit={this.handleSubmit}>
                        <div className="form-group row">
                            
                            <label for="username" className="col-2">
                                User-Name : 
                            </label>
                            <input placeholder="UserName" onChange={this.handleChange} type="text" name="username" value={this.state.data.username} id="username" className="col form-control"/>
                        </div>
                        <div className="form-group row">
                            <label for="password" className="col-2">
                                Password : 
                            </label>
                            <input placeholder="Password" onChange={this.handleChange} type="password" name="password" value={this.state.data.password} id="password" className="col form-control"/>
                        </div>
                        <div className="d-flex justify-content-center">
                        <button className="btn btn-primary ">LogIn</button>
                        </div>
                        <p>admin , 12345</p>
                    </form>
                </div>

           </div>
            
        </div> );
    }
}
 
export default Login;