import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';
class Thanks extends Component {
    state = {  }
   func=()=>{


       setTimeout(()=>{
        this.props.history.push('/shopping-kart-app');
       },3000);
       
   }
  
    render() { 
        const  styles={marginTop:150 , textAlign:"center"};
        
        return ( <div style={styles} className="container">
            <h3 > Thank you for choosing Us !</h3>
        <br></br>
            <p>You Will be automatically redirected to the menu-page in 3 seconds :)</p>
            
               { this.func() }
        </div> );
    }
}
 
export default Thanks;