import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';
import {  toast } from 'react-toastify';

class AddFood extends Component {
    state = { 
        data:{
            name:"",
            url:"",
            price:""
        }
     }
     handleChange=(e)=>{
        const data={...this.state.data}
        data[e.currentTarget.name]=e.currentTarget.value;
        this.setState({data});
     }
     handleSubmit=(e)=>{
         e.preventDefault();
         this.props.addItem(this.state.data.name,this.state.data.price,this.state.data.url);
         toast.success('Item added !');
         this.props.history.push("/shopping-kart-app");
     }
    render() { 
       
       const  styles={marginTop:100}
        return ( <div className="container" style={styles}>
    <div className="card">
    <div className="card-header">
      <b><i>  Add Item </i></b>
    </div>
        <div className="card-body m-2">
        <form onSubmit={this.handleSubmit}>
    
    <div className="form-group row">
        <label for="name" className="col-2">Name</label>
        <input onChange={this.handleChange} type="text" className="col form-control" id="name" name="name" value={this.state.data.name}/>
    </div>
    <div className="form-group row">
        <label for="url" className="col-2">Url</label>
        <input  onChange={this.handleChange} type="text" className="col form-control" id="url" name="url" value={this.state.data.url}/>
    </div>
    <div className="form-group row">
        <label for="price" className="col-2">Price</label>
        <input onChange={this.handleChange}  type="text" className="col form-control" id="price" name="price" value={this.state.data.price}/>
    </div>
    <button className="btn btn-primary"  type="submit">Add Item</button>
</form>
        </div>
        </div>

           
        </div> );
    }
}
 
export default AddFood;