import React, { Component } from 'react';
import Item from './item';
import '../../node_modules/bootstrap/dist/css/bootstrap.css';
import {Link} from 'react-router-dom';
class Items extends Component {
    state = {  }
 
    render() { 
        console.log('Items Called');
        const  styles={marginTop:100}
        return ( <div>
            <div className="container" style={styles}>
                
                <div className="row">
                    <div className="col-5"></div>
                    <div className="col">
                    <Link to="/add"><button className="btn btn-primary m-3">Add Item</button></Link>
                    </div>
                    
                </div>
                   <div className="row">
                   {this.props.it.map((i)=>{
                return (<Item uniItem={i} key={i.id} onIncrement={this.props.onIncrement} onRemove={this.props.onRemove}/>);
                     })}
                   </div> 
            </div>
           <div className="justify-content-center d-flex mt-3 mb-3">
           <Link to="/login">  <button className="btn btn-primary btn-lg">Check-Out</button></Link>
           </div>
        </div> );
    }
}
 
export default Items;